import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HTTP } from '@ionic-native/http';

/**
 * Generated class for the TheatherDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-theather-details',
  templateUrl: 'theather-details.html',
})
export class TheatherDetailsPage {
  public theatherDetails;
  public items;
  public showtimes;

  constructor(private http:HTTP, public navCtrl: NavController, public navParams: NavParams) {
    this.theatherDetails = navParams.get("theatherDetails");
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TheatherDetailsPage');
    console.log(this.theatherDetails);
    this.request();
  }

  request() {
    let key = 'w4m3xwur2n8je4nr4fhs44uh';
    let currentTime = new Date();
    let month = currentTime.getMonth() + 1;
    let day = currentTime.getDate();
    let year = currentTime.getFullYear();
    let date = year + '-' + month + '-' + day;
    let url = "https://data.tmsapi.com/v1.1/theatres/"+ this.theatherDetails.theatreId + "/showings?startDate="+ date + "&numDays=2&api_key=" + key;
    //param
    //headers
    console.log(url);
    this.http.get(url, {}, {})
      .then(response => {
        let data = JSON.parse(response.data);
        this.items = data;
        console.log(this.items);
      })

      .catch(error => {
        console.error(error);
      });
  }

  toggleSection(i) {
    this.items[i].open = !this.items[i].open;
  }
 
  toggleItem(i, j) {
    this.items[i].showtimes[j].open = !this.items[i].showtimes[j].open;
  }

}



