import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HTTP } from '@ionic-native/http';
import { StorageProvider } from '../../providers/storage/storage';
import { ToastController } from 'ionic-angular';


/**
 * Generated class for the MovieDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-movie-details',
  templateUrl: 'movie-details.html',
})
export class MovieDetailsPage {
  public movieDetails;
  public item;
  public ratings;
  public favorites = [];
  isFavorite = false;




  constructor(private toastCtrl: ToastController, private storage: StorageProvider, public http: HTTP, public navCtrl: NavController, public navParams: NavParams) {
    this.movieDetails = navParams.get("movieDetails");
    this.storage.isFavorite(this.movieDetails).then(isFav => {
      this.isFavorite = isFav;
    });
  }

  ionViewDidLoad() {
    console.log(this.movieDetails);
    let url = "https://www.omdbapi.com/?t=" + encodeURI(this.movieDetails.Title) + "&type=movie&plot=full&apikey=bb7201bc";
    console.log(url);
    this.http.get(url, {}, {})
      .then(response => {
        console.log("response", response);

        this.item = JSON.parse(response.data);
        console.log("data", this.item);
        this.ratings = this.item.Ratings;
        console.log(this.ratings);
      })


      .catch(error => {
        console.error(error);
      });

  }


  favorite() {
    this.storage.favorite(this.movieDetails).then(() => {
      this.isFavorite = true;
    });
    let toast = this.toastCtrl.create({
      message: 'Movie was added to favorites',
      duration: 1500,
      position: 'bottom'
    });

    toast.onDidDismiss(() => {
      console.log('Dismissed toast');
    });

    toast.present();

  }

  unfavorite() {
    this.storage.unfavorite(this.movieDetails).then(() => {
      this.isFavorite = false;
    });
    let toast = this.toastCtrl.create({
      message: 'Movie was removed from favorites',
      duration: 1500,
      position: 'bottom'
    });

    toast.onDidDismiss(() => {
      console.log('Dismissed toast');
    });

    toast.present();
  }




}



