import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';

/*
  Generated class for the StorageProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class StorageProvider {

  constructor(public storage:Storage) {
    console.log('Hello StorageProvider Provider');
  }

  isFavorite(movie) {
    return this.getAllFavorites().then(result => {
      return result && result.indexOf(movie) !== -1;
    });
  }
 
  favorite(movie) {
    return this.getAllFavorites().then(result => {
      if (result) {
        movie.favorite = 'true';
        result.push(movie);
        return this.storage.set('favorites', result);
      } else {
        return this.storage.set('favorites', [movie]);
      }
    });
  }
 
  unfavorite(movie) {
    return this.getAllFavorites().then(result => {
      if (result) {
        let index = result.indexOf(movie);
        result.splice(index, 1);
        return this.storage.set('favorites', result);
      }
    });
  }
 
  getAllFavorites() {
    return this.storage.get('favorites');
  }
}



